import React from 'react';
import APPCONFIG from 'constants/appConfig';
import Input from '@material-ui/core/Input';
import QueueAnim from 'rc-queue-anim';
import {reactLocalStorage} from 'reactjs-localstorage';
import axios from 'axios';
import InputLabel from '@material-ui/core/InputLabel';


class Login extends React.Component {
  constructor() {
    super();
    this.state = {
      brand : APPCONFIG.brand,
      user : "",
      password : ""
    };
  }
  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  submitHandle = (e) => {
    const payload = {
      "username" : this.state.user,
      "password" : this.state.password
    }
  
    axios.post('http://13.229.249.101/dashboard/api/token', payload)
    // axios.post('http://localhost:8000/token', payload)
    .then((response) => {
        const token = response.data.token;
        reactLocalStorage.set('auth', token);
        window.location.href="/#/admin";
    })
    .catch((error) => {
        console.log(error);
    });  
    e.preventDefault(); 
  }

  render() {
    return (
      <div className="body-inner">
        <div className="card card-white">
          <div className="card-content">

            <section className="logo text-center">
              <h1><a href="#/">{this.state.brand}</a></h1>
            </section>

            <form className="form-horizontal" onSubmit = {this.submitHandle}>
              <fieldset>
                <div className="form-group">
                <InputLabel htmlFor="user">Username</InputLabel>
                <Input
                    inputProps={{name: 'user'}}
                    onChange={this.handleChange}
                    value={this.state.user}                
                    id="user"
                    fullWidth
                />
                </div>
                <div className="form-group mt-2">
                <InputLabel htmlFor="password">Password</InputLabel>
                <Input
                    inputProps={{name: 'password', type: 'password'}}
                    onChange={this.handleChange}
                    value={this.state.password}  
                    id="password"
                    fullWidth
                />
                </div>
              </fieldset>
              <div className="card-action border-0 text-right">
                <button type="submit" className="btn btn-outline-info px-4">Login</button>
              </div>
            </form>
          </div>
          
        </div>

        <div className="additional-info">
         
        </div>

      </div>
    );
  }
}

const Page = () => (
  <div className="page-login">
    <div className="main-body">
      <QueueAnim type="bottom" className="ui-animate">
        <div key="1">
          <Login />
        </div>
      </QueueAnim>
    </div>
  </div>
);

export default Page;
